import React, { Component } from 'react';
import './App.css';
import * as THREE from 'three';

class App extends Component {

  constructor() {
    super();
    
    const boxWidth = 15;
    const boxHeight = 20;
    const boxDepth = 10;

    const numBalls = 200;
    const ballRadius = 1.5;

    // const boxMaterial = new THREE.MeshLambertMaterial({color: 0x808080, transparent: true, opacity: 0.5})
    // const box =
    //   new THREE.Mesh(
    //     new THREE.BoxGeometry(boxWidth, boxHeight, boxDepth),
    //     boxMaterial);
    
    const balls = []
    const ballMaterial = new THREE.MeshLambertMaterial({color: 0x00ff00})

    for (let i = 0; i < numBalls; i++) {
      const ball = 
        new THREE.Mesh(
          new THREE.SphereGeometry(ballRadius), ballMaterial
        );
      ball.position.x = (Math.random() - 0.5) * boxWidth;
      ball.position.y = (Math.random() - 0.5) * boxHeight;
      ball.position.z = (Math.random() - 0.5) * boxDepth;
      balls.push({
        mesh: ball,
        velocity: new THREE.Vector3(0, 0, 0)
      });
    }

    const scene = new THREE.Scene();
    const light = new THREE.PointLight();
    // scene.add(box);
    scene.add(light);
    for (let ball of balls) {
      scene.add(ball.mesh);
    }
    // scene.add(new THREE.AxesHelper(5));


    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    camera.position.x = 0;
    camera.position.y = 0;
    camera.position.z = 20;

    light.position.x = camera.position.x;
    light.position.y = camera.position.y;
    light.position.z = camera.position.z;

    
    const renderer = new THREE.WebGLRenderer({antialias: true});
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.domElement.style.width = `${window.innerWidth}px`;
    renderer.domElement.style.height = `${window.innerHeight}px`;

    renderer.render(scene, camera);

    document.body.appendChild(renderer.domElement);

    const externalAcceleration = new THREE.Vector3(0, 0, 0);

    window.addEventListener('devicemotion', (e) => {
      externalAcceleration.set(
        e.accelerationIncludingGravity.x * 5,
        e.accelerationIncludingGravity.y * 5,
        e.accelerationIncludingGravity.z * 5);
    });

    // window.addEventListener('mousemove', (e) => {
    //   externalAcceleration.set(
    //     (e.x - window.innerWidth / 2) / 20,
    //     (window.innerHeight / 2 - e.y) / 20,
    //     0
    //   );
    // })

    const frameDuration = 1/30;
    const collisionElasticConstant = 50;

    function frame() {

      for (let ball of balls) {
        // apply gravity
        ball.velocity.addScaledVector(externalAcceleration, frameDuration);

        // apply wall collisions
        const wallCollisionAcceleration = new THREE.Vector3();
        if (ball.mesh.position.x < - boxWidth / 2 + ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(-boxWidth / 2 + ballRadius - ball.mesh.position.x, 0, 0), collisionElasticConstant);
        }
        if (ball.mesh.position.x > boxWidth / 2 - ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(boxWidth / 2 - ballRadius- ball.mesh.position.x, 0, 0), collisionElasticConstant);
        }
        if (ball.mesh.position.y < - boxHeight / 2 + ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(0, -boxHeight / 2 + ballRadius - ball.mesh.position.y, 0), collisionElasticConstant);
        }
        if (ball.mesh.position.y > boxHeight / 2 - ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(0, boxHeight / 2 - ballRadius - ball.mesh.position.y, 0), collisionElasticConstant);
        }
        if (ball.mesh.position.z < - boxDepth / 2 + ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(0, 0, -boxDepth / 2 + ballRadius - ball.mesh.position.z), collisionElasticConstant);
        }
        if (ball.mesh.position.z > boxDepth / 2 - ballRadius) {
          wallCollisionAcceleration.addScaledVector(new THREE.Vector3(0, 0, boxDepth / 2 - ballRadius - ball.mesh.position.z), collisionElasticConstant);
        }

        ball.velocity.addScaledVector(wallCollisionAcceleration, frameDuration);

        // apply ball-to-ball collisions
        for (let ball2 of balls) {
          if (ball !== ball2) {
            const dist = ball.mesh.position.distanceTo(ball2.mesh.position);
            if (dist < 2 * ballRadius) {
              const squish = 2 * ballRadius - dist;
              const collisionAccel = (new THREE.Vector3())
                .subVectors(ball.mesh.position, ball2.mesh.position)
                .setLength(squish * collisionElasticConstant);
              ball.velocity.addScaledVector(collisionAccel, frameDuration)
            }
          }
        }

        // apply air resistance
        ball.velocity.multiplyScalar(0.95);

        // update position
        ball.mesh.position.addScaledVector(ball.velocity, frameDuration)
      };
      renderer.render(scene, camera);

      window.requestAnimationFrame(frame);
    }

    window.requestAnimationFrame(frame);
  }

  render() {
    return (
      <div className="App" />
    );
  }
}

export default App;



